import cuCustom from '@/colorui/components/cu-custom.vue'
export default {
	data() {
		return {
			isBottom: false,
			InputBottom: 0,
			pages: [],
			plaza_id: 0,
			baseUrl: 'http://47.104.29.236:9999/api/v1/',
			// 评论
			speakList: [],
			text: '',
			url: '/pages/plaza/plaza'
		}
	},
	onLoad: function(option) {
		this.plaza_id = option.plazaid
		this.getPlaza(this.plaza_id)
		this.getSpeak(this.plaza_id)
	},
	methods: {
		getPlaza(plaza_id) {
			uni.request({
				url: this.baseUrl + 'plaza_details/' + plaza_id,
				method: 'GET',
				data: {},
				success: res => {
					// console.log(res.data)
					this.pages = res.data.data
					// console.log(this.pages)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getSpeak(plaza_id) {
			uni.request({
				url: this.baseUrl + '/getSpeak',
				data: {
					id: plaza_id
				},
				success: (res) => {
					this.speakList = res.data
					// console.log(this.speakList)
					let time = parseInt(new Date().getTime() / 1000)

					function transformTime(timestamp = +new Date()) {
						if (timestamp) {
							var time = new Date(timestamp);
							var y = time.getFullYear();
							var M = time.getMonth() + 1;
							var d = time.getDate();
							var h = time.getHours();
							var m = time.getMinutes();
							var s = time.getSeconds();
							return y + '-' + addZero(M) + '-' + addZero(d) + ' ' + addZero(h) + ':' + addZero(m) + ':' + addZero(s);
						} else {
							return '';
						}
					}

					function addZero(m) {
						return m < 10 ? '0' + m : m;
					}

					this.speakList.forEach(v => {
						// v.plaza_time = time - v.plaza_time
						v.plaza_time = transformTime(v.plaza_time * 1000);
					})
				},
				fail: (err) => {
					console.log(err)
				}
			})
		},
		addSpeak() {
			console.log(this.pages)
			let time = parseInt(new Date().getTime() / 1000)
			let user_id = uni.getStorageSync('user_id')
			let info = {
				plaza_id: this.plaza_id,
				commont_id: user_id,
				message: this.text,
				beCommont_id: '',
				plaza_time: time
			}
			info.beCommont_id = this.pages[0].plazaUser_id
			// console.log(info)
			if (this.text !== '') {
				uni.request({
					url: this.baseUrl + '/addSpeak',
					data: info,
					header: {
						"content-type": "application/x-www-form-urlencoded"
					},
					method: 'POST',
					success: (res) => {
						// console.log(res.data)
					},
					fail: (err) => {
						console.log(err)
					}
				})
			} else {
				uni.showToast({
					title: '请填写您的 评论',
					image: '../../static/toast/error.png'
				})
			}
		},
		click(index) {
			console.log(this.speakList[index].isClick)
			this.speakList[index].isClick = !this.speakList[index].isClick
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		// 跳转到广场个人
		openpersonal() {
			uni.navigateTo({
				url: '../plaza_personal/plaza_personal'
			});
		},
	}
}
